let backTopSelector = document.querySelector('.scroll-top')

if (backTopSelector) {
	backTopSelector.addEventListener('click', event => {
		event.preventDefault()

		window.scroll({ top: 0, left: 0, behavior: 'smooth' })
	}, false)

	window.addEventListener('scroll', () => {
		let scrolled = document.scrollingElement.scrollTop || 0

		if (scrolled > 300) {
			backTopSelector.classList.add('show')
		} else {
			backTopSelector.classList.remove('show')
		}
	})
}
